import React, { useState } from "react";
import ReactQuill from "react-quill";
import { convert } from "html-to-text";
import axios from "axios";
import "react-quill/dist/quill.snow.css";
import "../App.css";
import htmlDocx from 'html-docx-js/dist/html-docx';

function EditorScreen({ answers, proposalText, onBack }) {
  const [docContent, setDocContent] = useState("");
  const [generatedAnswers, setGeneratedAnswers] = useState(answers.map((a) => a.answer));
  const [currentIndex, setCurrentIndex] = useState(0);
  const [loading, setLoading] = useState(false);
  const [documentTitle, setDocumentTitle] = useState("Untitled Document");

  const currentQuestion = answers[currentIndex].question;

  const icon = (filename) => `${process.env.PUBLIC_URL}/${filename}`;

  const handleUse = () => {
    const fullInsert = `
      <p><strong>${answers[currentIndex].question}</strong></p>
      <p>${generatedAnswers[currentIndex]}</p>
      <br/>
    `;
    setDocContent((prev) => prev + fullInsert);
  };

  const handleCopy = async () => {
    try {
      await navigator.clipboard.write([
        new ClipboardItem({
          "text/html": new Blob([docContent], { type: "text/html" }),
        }),
      ]);
      alert("Copied!");
    } catch (err) {
      alert("Copy failed: " + err);
    }
  };

  const handleDownload = () => {
    const editorElement = document.querySelector(".ql-editor");
    const contentHtml = editorElement.innerHTML;

    const html = `
      <html>
        <head><meta charset="utf-8" /></head>
        <body>${contentHtml}</body>
      </html>
    `;

    const docxBlob = htmlDocx.asBlob(html);
    const link = document.createElement("a");
    link.href = URL.createObjectURL(docxBlob);
    const safeTitle = documentTitle.trim() === "" ? "Untitled" : documentTitle.trim().replace(/[^a-z0-9]/gi, "_");
    link.download = `${safeTitle}.docx`;
    link.click();
  };

  const handleRegenerate = async () => {
    setLoading(true);
    try {
      const apiBaseUrl = process.env.REACT_APP_API_URL;
      const res = await axios.post(`${apiBaseUrl}/regenerate`, {
        question: currentQuestion,
        proposal: proposalText,
      });

      const updated = [...generatedAnswers];
      updated[currentIndex] = res.data.answer;
      setGeneratedAnswers(updated);
    } catch (err) {
      console.error(" Regenerate error:", err);
      alert("Failed to regenerate answer.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="editor-screen">
      <button onClick={onBack} className="back-to-upload">
        <img src={icon("file-icon-blue.svg")} alt="File Icon" className="back-icon" />
        <span>Create new application</span>
      </button>

      <div className="editor-header">
        <img
          src={icon("answerpallogo.svg")}
          alt="AnswerPal Logo"
          className={`answerpal-logo ${loading ? "spinning" : ""}`}
        />
        <h1 className="answerpal-heading">Answer Pal</h1>
      </div>

      <div className="editor-body">
        <div className="left-panel">
          <div className="editor-wrapper">
            <input
              type="text"
              className="document-title-input"
              placeholder="Untitled Document"
              value={documentTitle}
              onChange={(e) => setDocumentTitle(e.target.value)}
            />
            <ReactQuill
              value={docContent}
              onChange={(val) => setDocContent(val)}
              className="quill-editor"
            />
          </div>
          <div className="left-footer">
            <div className="button-row">
              <button onClick={handleCopy} className="copy-button">
                <img src={icon("copy-icon-blue.svg")} alt="Copy Icon" className="copy-icon" />
                Copy
              </button>

              <div className="tooltip">
                <button className="download-button" onClick={handleDownload}>
                  <img src={icon("download-icon.svg")} alt="Download" className="download-icon" />
                </button>
                <span className="tooltiptext">Download as .docx</span>
              </div>
            </div>

            <div className="left-word-count">
              Word Count: {convert(docContent, { wordwrap: false }).split(/\s+/).filter(Boolean).length}
            </div>
          </div>
        </div>

        <div className="right-panel">
          <div className="question-dropdown">
            <select
              onChange={(e) => setCurrentIndex(parseInt(e.target.value))}
              value={currentIndex}
            >
              {answers.map((a, i) => {
                const cleaned = a.question.replace(/^(\s*\(?[Qq]?\d+[\).]?\s*)/, "").trim();
                return (
                  <option value={i} key={i}>
                    Q{i + 1}: {cleaned}
                  </option>
                );
              })}
            </select>
          </div>

          <textarea
            value={generatedAnswers[currentIndex]}
            onChange={(e) => {
              const updated = [...generatedAnswers];
              updated[currentIndex] = e.target.value;
              setGeneratedAnswers(updated);
            }}
            className="generated-textarea"
          />

          <div className="right-footer">
            <div className="button-nav-row">
              <button onClick={handleRegenerate} disabled={loading} className="regenerate-button">
                <img src={icon("regenerate-icon.svg")} alt="Regenerate Icon" className="button-icon" />
                {loading ? "Regenerating..." : "Regenerate"}
              </button>

              <button onClick={() => setCurrentIndex((i) => Math.max(i - 1, 0))} className="nav-button">
                <img src={icon("arrow-left.svg")} alt="Previous" />
              </button>

              <button onClick={() => setCurrentIndex((i) => Math.min(i + 1, answers.length - 1))} className="nav-button">
                <img src={icon("arrow-right.svg")} alt="Next" />
              </button>

              <button onClick={handleUse} className="use-button">
                <img src={icon("use-icon.svg")} alt="Use Icon" className="button-icon" />
                Use
              </button>
            </div>
          </div>

          <div className="right-word-count">
            Word Count: {generatedAnswers[currentIndex].split(/\s+/).filter(Boolean).length}
          </div>
        </div>
      </div>
    </div>
  );
}

export default EditorScreen;
