import React, { useState } from "react";
import axios from "axios";
import "../App.css";

function UploadScreen({ onGenerateComplete }) {
  const [questionsFile, setQuestionsFile] = useState(null);
  const [proposalFile, setProposalFile] = useState(null);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!questionsFile || !proposalFile) {
      setError("Please upload both Questions and Proposal files.");
      return;
    }

    setLoading(true);
    setError("");

    const formData = new FormData();
    formData.append("questions", questionsFile);
    formData.append("proposal", proposalFile);

    try {
      const apiBaseUrl = process.env.REACT_APP_API_URL;

      console.log(" API base URL is:", apiBaseUrl);

      const response = await axios.post(`${apiBaseUrl}/generate`, formData);
      onGenerateComplete(response.data.answers, response.data.proposal);
    } catch (err) {
      console.error(" Error calling /generate:", err);
      setError("Failed to generate answers.");
    } finally {
      setLoading(false);
    }
  };

  const icon = (filePresent) =>
    `${process.env.PUBLIC_URL}/${filePresent ? "tick-icon.svg" : "file-icon.svg"}`;

  return (
    <div className="upload-overlay">
      <div className="overlay" />
      <div className="upload-box">
        <div className="logo-container">
          <img
            src={`${process.env.PUBLIC_URL}/answerpallogo.svg`}
            alt="AnswerPal Logo"
            className={`answerpal-logo ${loading ? "spinning" : ""}`}
          />
          <h1 className="answerpal-heading">Answer Pal</h1>
          <p>
            Upload your application questions document and project document for me to help generate answers for you!
          </p>
        </div>

        <form onSubmit={handleSubmit}>
          {/* Questions */}
          <div className="upload-section">
            <label>Upload your <strong>application questions</strong> document here:</label>
            <label className="drop-zone">
              <img
                src={icon(questionsFile)}
                alt="File Icon"
              />
              {!questionsFile && (
                <span>Choose file <em>(.txt, .pdf, .docx)</em></span>
              )}
              <input
                type="file"
                accept=".pdf,.docx,.txt"
                onChange={(e) => setQuestionsFile(e.target.files[0])}
                style={{ display: "none" }}
              />
            </label>
            {questionsFile && (
              <div className="selected-filename">
                Selected: <strong>{questionsFile.name}</strong>
                <button
                  type="button"
                  className="remove-file"
                  onClick={() => setQuestionsFile(null)}
                >
                  
                </button>
              </div>
            )}
          </div>

          {/* Proposal */}
          <div className="upload-section">
            <label>Upload your <strong>project information</strong> document here:</label>
            <label className="drop-zone">
              <img
                src={icon(proposalFile)}
                alt="File Icon"
              />
              {!proposalFile && (
                <span>Choose file <em>(.txt, .pdf, .docx)</em></span>
              )}
              <input
                type="file"
                accept=".pdf,.docx,.txt"
                onChange={(e) => setProposalFile(e.target.files[0])}
                style={{ display: "none" }}
              />
            </label>
            {proposalFile && (
              <div className="selected-filename">
                Selected: <strong>{proposalFile.name}</strong>
                <button
                  type="button"
                  className="remove-file"
                  onClick={() => setProposalFile(null)}
                >
                  
                </button>
              </div>
            )}
          </div>

          <button
            className={`generate-button ${questionsFile && proposalFile ? "active" : ""}`}
            type="submit"
            disabled={loading || !questionsFile || !proposalFile}
          >
            {loading ? "Generating..." : "Generate Answers"}
          </button>

          {error && <div className="error-message">{error}</div>}
        </form>
      </div>
    </div>
  );
}

export default UploadScreen;
